<template>
    <div>
        {{showStock}}
    </div>
</template>

<script>

import {mapActions, mapGetters, } from 'vuex'
export default {
    computed:{
        ...mapGetters(['showStock'])
    },
    methods:{
        ...mapActions(['stockFetch']),
    },
    async mounted(){
        this.stockFetch();
    }
}
</script>

